.intro {
  margin: 3rem; }

.social-media img {
  height: 1.5rem;
  margin: 0rem 2rem; }

.projects {
  padding: 0;
  overflow: hidden; }

.project img {
  width: 25vw; }

@media screen and (min-width: 576px) {
  h1 {
    text-align: left;
    margin-left: 8rem;
    margin-top: 0rem; }
  .black {
    color: #000000; }
  .text-link {
    font-size: 1.5rem;
    margin-left: 0.3rem; }
  .social-media {
    text-align: left;
    margin-left: 8rem; }
    .social-media img {
      height: 1.5rem;
      margin: 0rem 3rem 0rem 0rem; }
  .intro {
    text-align: left;
    margin: 5rem;
    font-weight: 500; }
  .row {
    margin-left: 6rem;
    margin-right: 6rem; } }

@media screen and (min-width: 768px) {
  .intro {
    margin: 0rem 20rem 2rem 8rem;
    font-size: 1.5rem; } }
