body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  scroll-behavior: smooth; }

body {
  text-align: center;
  font-family: 'Avenir', sans-serif; }

h1, h2 {
  color: #448483; }

h1 {
  font-size: 4.5rem; }

p {
  font-size: 1.5rem;
  font-weight: 300; }

.block {
  margin-top: 6rem; }

.paragraph {
  margin: 15vw 5vw; }

.title {
  font-size: 3.5rem; }

.light {
  font-weight: 300; }

.italic {
  font-style: italic; }

.big-picture {
  max-width: 100%; }

@media screen and (min-width: 576px) {
  .title {
    font-size: 4rem; }
  p {
    font-size: 1.5rem; }
  .paragraph {
    margin: 15vw; } }

@media screen and (min-width: 768px) {
  .title {
    font-size: 6rem; }
  p {
    font-size: 2rem; } }

.header {
  min-height: 10vh;
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  border-bottom: 2px solid #448483;
  margin-bottom: 9rem; }

.links {
  position: absolute;
  right: 8rem;
  bottom: 0px; }

a {
  font-size: 1.2rem;
  font-weight: 600;
  margin: 10px;
  text-decoration: none;
  color: #448483; }

a:hover {
  color: #4ADAE8;
  text-decoration: none; }

.header-link {
  margin: 0rem 0rem 0rem 4rem; }

.favicon {
  height: 3rem;
  position: absolute;
  left: 8rem;
  margin: 0rem; }

.footer {
  margin-top: 20vh;
  padding: 15vw 3vw;
  color: #fff;
  background: #448483; }

.footerh2 {
  color: #fff;
  font-size: 1.5rem; }

p {
  font-size: 1.2rem; }

@media screen and (min-width: 576px) {
  .footer {
    padding: 1rem; }
  .footer-text {
    font-size: 1rem;
    font-weight: 700; }
  p {
    font-size: 1.5rem;
    margin-bottom: 0rem; } }

.text {
  margin-left: 3rem; }

h1 {
  font-size: 4rem;
  margin-top: 6rem; }

p {
  font-size: 1.25rem; }

@media screen and (min-width: 576px) {
  .text-link {
    font-size: 1.25rem;
    margin: 0;
    margin-left: 0.3rem; } }

.intro {
  margin: 3rem; }

.social-media img {
  height: 1.5rem;
  margin: 0rem 2rem; }

.projects {
  padding: 0;
  overflow: hidden; }

.project img {
  width: 25vw; }

@media screen and (min-width: 576px) {
  h1 {
    text-align: left;
    margin-left: 8rem;
    margin-top: 0rem; }
  .black {
    color: #000000; }
  .text-link {
    font-size: 1.5rem;
    margin-left: 0.3rem; }
  .social-media {
    text-align: left;
    margin-left: 8rem; }
    .social-media img {
      height: 1.5rem;
      margin: 0rem 3rem 0rem 0rem; }
  .intro {
    text-align: left;
    margin: 5rem;
    font-weight: 500; }
  .row {
    margin-left: 6rem;
    margin-right: 6rem; } }

@media screen and (min-width: 768px) {
  .intro {
    margin: 0rem 20rem 2rem 8rem;
    font-size: 1.5rem; } }

h1 {
  font-size: 3rem; }

h3 {
  font-size: 2.5rem;
  margin-bottom: 4rem; }

@media screen and (min-width: 576px) {
  h1 {
    font-size: 6rem; }
  .subtitle {
    text-align: left;
    margin-left: 8rem; }
  .paragraph {
    text-align: left;
    margin-left: 8rem; } }

